
 .timer {
  display: flex;
  position: absolute;
  font-size: 60px;
}
.field {
  position: relative;
  min-width: 60px;
  margin-left: 10px;
  margin-right: 10px;
  padding-bottom: 15px;
  text-align: center;
}
.label {
   content: attr(data-title);
  display: block;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  font-size: 14px;
}  

.field:not(:last-child)::after {
  content: ':';
  position: absolute;
  right: -15px;
}